<template>
  <div v-if="bedInfoAll.length > 0"
       id="apartMent_scorll">
    <div v-for="(item,i) in bedInfoAll"
         :key="i"
         @click="goNext(item)">
      <div class="content">
        <div class="content_left">
          <img :id="'Img' + item.id"
               style="border-radius:50%;width:36px;height:36px;margin-right:6px;" />
          <canvas :id="'headImage' + item.id"
                  style="display: none"></canvas>
          <div v-if="item.order">
            <div style="font-weight:bold;">{{ item.order.roomUser }}</div>
            <div class="c_98">{{ item.order.startTime | dataFilter('YYYY-MM-DD') }}至{{ item.order.endTime | dataFilter('YYYY-MM-DD') }}</div>
            <div v-if='item.roomScale'
                 class="c_98">{{ item.roomScale.scaleName }}-{{ item.roomSn }}</div>
          </div>
        </div>
        <div class="content_right">
          <div style="font-size:14px;">{{ i+1 }}床</div>
          <van-icon name="arrow"
                    color="#989898" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessOrderroomByroombed } from '@/api/api_agreement';
import { draw } from '@/utils/avatar.js';
export default {
    data() {
        return {
            bedInfoAll: []
        };
    },
    created() {
        this.$store.commit('setHeader', '床位选择');
        this.getBedInfo(this.$route.query.roomId);
    },
    methods: {
        // 展开信息
        getBedInfo(roomId) {
            GetBusinessOrderroomByroombed({ roomId: roomId }).done((res) => {
                this.bedInfoAll = res.data;
                this.$nextTick(function () {
                    this.bedInfoAll.forEach((item) => {
                        if (!item.avatar) {
                            draw(40, 40, 20, 'headImage' + item.id, 'Img' + item.id, item.order.roomUser, 1);
                        }
                    });
                });
            });
        },
        // 跳至订单详情
        goNext(item) {
            this.$router.push({
                path: '/rentInfo_long',
                query: {
                    orderRoomId: item.id
                }
            });
        }
    }
};
</script>
<style scoped>
#apartMent_scorll {
    position: fixed;
    top: 50px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: scroll;
}
.content {
    font-size: 15px;
    color: #000;
    display: flex;
    justify-content: space-between;
    padding: 10px 16px;
    align-items: center;
    background: #fff;
    margin-top: 10px;
}
.content_left {
    display: flex;
    text-align: left;
    line-height: 22px;
}
.content_right {
    display: flex;
    align-items: center;
}
.header_img {
    width: 40px;
    height: 40px;
    border-radius: 30px;
    margin-right: 10px;
}
.c_98 {
    color: #989898;
    font-size: 14px;
}
</style>



